nu-6{-webkit-animation:growDown 300ms ease-in-out forwards;animation:growDown 300ms ease-in-out forwards;transform-origin:top center}@-webkit-keyframes growDown{0%{transform:scaleY(0)}80%{transform:scaleY(1.1)}100%{transform:scaleY(1)}}@keyframes growDown{0%{transform:scaleY(0)}80%{transform:scaleY(1.1)}100%{transform:scaleY(1)}}

